import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from '../Button/Button.jsx';
import cn from 'classnames';
import clx from './Basket.module.scss';

const Basket = () => {
  return (
    <div className={clx.basket}>
      <div className={clx.title}> :</div>
      <ul className={clx.body}>
        <li className={clx.info}>
          <div className={clx.type}> </div>
          <div className={clx.dotted}></div>
          <div className={clx.select}>,  42</div>
        </li>
      </ul>
      <div className={clx.total}>:  8 000  12 000 </div>
      <Link to={'/'} className={clx.btn}>
        <Button> </Button>
      </Link>
    </div>
  );
};

export { Basket };
